@include media-breakpoint-down(xxl) {
    .container-xl, .container-lg, .container-md, .container-sm, .container{
        max-width: 1200px;
    }
}

@include media-breakpoint-down(xl) { 
    
    .main-nav {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        width: 270px;
        padding: 50px 25px !important;
        min-height: 100vh;
        background-color: $primary;
        justify-content: center;
        flex-wrap: wrap;       
        overflow: auto;
        opacity: 0;
        transform: translateX(-100%);
        visibility: hidden;
        box-shadow: 5px 0px 5px rgba($black, 0.1);
        transition: all 0.3s ease-out;
        .btn-nav{
            margin-top: 12px !important;
            margin-left: 0;
            width: 100%;
        }       
        
       > ul {
            padding-right: 0;
            display: block;
            > li {
                flex: 1 1 100%;
                >a {
                    text-align: left;
                    display: block;
                    padding: 12px 0; 
                    color: $white;  
                    span{                      
                        i{
                            position: static;                           
                        }
                    }
                    &:hover {                                    
                        color: $primaryLight;
                        font-weight: 700;                       
                    }                
                }  

            }
        }

       > .social-nav{
        margin: 0 -20px !important;
           > li{
                flex: 0 0 auto;
                a{
                    padding: 10px 20px;
                }
            }
        }
    }

    .menu-active {
        .main-nav {           
            visibility: visible;
            opacity: 1;
            transform: none;
        }
    }





    

}

@include media-breakpoint-down(lg) {   
    footer {
        .border-start
        {
            border-left: 0!important;
        }
    }
    .tabContent{
        min-height: auto;
        padding: 0;
        border-bottom-left-radius: 0;
        margin-right: 0;
        .tab-item
        {
            display: block;
            border-bottom: 1px solid $border;
            padding: 20px;
            transition: .3s ease-in-out;
            &:last-child
            {
                border-bottom: 0;
            }
            h4
            {
                position: relative;
                // margin-bottom: 0px;
                // padding-right: 40px;
                @include font-size(15px);
                margin: -20px 0;
                padding: 20px 40px 20px 0;                
            }
            
        }
    }
    .pricingNew .plan{
        width: auto;
    }
}

@include media-breakpoint-down(md) {
    .teamBox{
        width: 95%;
    }
    :root
    {
        --search-bar-margin: 30px;
    }
    .logo
    {
        width: 140px;
        display: block;
    }
    main 
    {
        padding-top: 68px;
    }
    .section 
    {
        padding: 45px 0;
    }
    .hero-pagination
    {
        left: 0!important;
        span
        {
            margin: 6px 10px!important;
        }
    }
    .bigTxt
    {
        @include font-size(15px);
    }
    .section-process 
    {
        .processBox
        {
            padding: 25px 0 60px;
        }
        .autoplay-progress
        {
            width: 70px;
            height: 70px;
            top: auto;
            left: 50%;
            right: auto;
            transform: translateX(-50%);
            bottom: -35px;
            .swiper-pagination-fraction
            {
                @include font-size(14px);
            }
            
        }
    }
    .swiper-button-prev, .swiper-button-next
    {
        width: 50px;
        height: 50px;
        &:after
        {
            --swiper-navigation-size: 30px;
        }
    }
    .tabLink{
        display: none;
    }
    .autoplay-progress{
        display: none !important;
    }
    .section-testimonials .processBox{
        padding: 30px 20px 40px;
        @include font-size(18px);
    }
    .tabContent{
        margin: 0 -20px;
        .tabInfo{
            padding: 20px 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    
    .popUp .teamImg{width: 100%;}
    .wareHouse-section1 .mapBox .vIconBox h3, .wareHouse-section1 .mapBox .vIconBox .h3{
        @include font-size(18px);
    }
    .wareHouse-section1 .mapBox .vIconBox h3 span, .wareHouse-section1 .mapBox .vIconBox .h3 span{
        @include font-size(14px);
    }
    .direction-wrapper>div:nth-child(even) .card-content{
        text-align: left;
    }
    footer .footer-end{
        font-size: 12px;
    }  
    .btn.btn-xl {
        padding: 8px 25px;
    }
    .section-process .processBox{
        font-size: 25px;
    }
    body, .column-type-content ul, .content-ul ul, .faq-card .faq-content{
        font-size: 13px;
    }
    .faq-menu .middle-bar ul li a{font-size: 15px;}
    .column-type-content ul li{padding: 4px 0;}
    .faq-card+.faq-card {
        margin-top: 10px;
    }
}

