.login-container{   
    position: relative; 
    @include media-breakpoint-down(lg){
        // background-color: $primaryLight;
        display: flex;
        min-height: 100vh;
        .login-wrap{
            margin: auto;
            padding-top: 50px;
        }
    }
    .login-img{
        height: 100vh;       
        min-height: 100%;        
        img{
            object-fit: cover;
            object-position: top center;
        }
    }
    .login-logo{
        position: absolute;
        left: 100px;
        top: 50px;
        width: 180px;  
        mix-blend-mode: multiply;    
        img{
            width: 100%;
        }
        @include media-breakpoint-down(lg){
            position: static;
            text-align: center;
            margin: auto;
        }
    }
}

.login-box{
    width: 400px;  
    padding: 50px 20px; 
    h2{
        margin-bottom: 1.1rem;
    }
    @include media-breakpoint-down(lg){
        padding-top: 20px; 
    }
    @include media-breakpoint-down(sm){
        width: 100%;       
    }
}

.form-field{
    margin-bottom: 1rem;
}

.form-control,.form-select{     
    height: 40px; 
    font-size: 14px;
    background-color: rgba($white, 0.50);
    &.form-control-lg{
        height: 48px; 
        font-size: 1rem;
    }
    &.form-control-sm{
        height: 36px;
        font-size: 13px;
    }
    &.form-control-checkbox{
        padding: 0;
        border: 0;
        box-shadow: 0;
        display: flex;
        align-items: center;
    }
}