:root {
    scroll-behavior: auto;    
}

::selection {
    background: $primary;
    color: $white;
}

::-webkit-input-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

:-moz-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

::-moz-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

:-ms-input-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

a {    
    color: $primaryLight;
    &:hover{
        color: $secondary;
    }
}

b,
strong {
    font-weight: 700;
}


h1 {
    @include font-size($h1size)
}

h2 {
    @include font-size($h2size);
    color: $primary;
    span{
        color: $secondary;
    }
}

h3 {
    @include font-size($h3size)
}

h4 {
    @include font-size($h4size)
}

h1,
h2,
h3,
h4,
h5 {
    font-family: $headingFont;
    font-weight: 700;
}

.btn{
    @include font-size(18px);
    font-weight: 600;    
    border-radius: 50px;
    padding: 12px 25px;
    min-width: 212px;
    .btn-search
    {
        letter-spacing: 0.100em;
    }
    &.btn-sm
    {
        @include font-size(14px);
        padding: 8px 20px;
        font-weight: normal;
        letter-spacing: 0.050em;
        min-width: 140px;
    }
    &.btn-lg{       
        @include font-size(26px);    
    }  
    &.btn-xl
    {
        padding: 17px 25px;
    }
    &.btn-secondarylight
    {
        background-color: $secondaryLight;
        &:hover
        {
            background-color: rgba($secondaryLight, 0.8);
        }
    }
}

.learn-more{
    text-decoration: none;
    font-weight: 600;
    @include font-size(20px);
    color: $primaryLight;
    position: relative;
    display: inline-flex;
    align-items: center;
    transition: color 0.3s;
    i{
        margin-left: 12px;
        transition: transform 0.3s;
    }
    &:hover{
        color: $secondary;
        i{
            transform: translateX(5px);
        }
    }
}


body {
    font-family: $bodyFontFamily;
    line-height: $bodyLineHeight;
    overflow-x: hidden;
    color: $bodyTextColor;
    position: relative;
    font-size: $bodyFontSize;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

.wrapper{
    overflow: hidden;   
    @include media-breakpoint-up(xl){
        //padding-top: 165px;
    }
}
.topBar{
    color: $white;
    padding: 5px 0;
    a{
        color: $white;
        text-decoration: none;
    }
}
.header-main {
    padding: 7px 0;
    z-index: 1000;
    background-color: $white;  
    margin-right: var(--fancybox-scrollbar-compensate);
    &.active{
        left: 0;
        right: 0;
        top: 0;
        position: fixed;
        padding: 7px 0;
        box-shadow: $headerShadow;
        background-color: rgba($white,0.8);  
        backdrop-filter: blur(20px);
        @include media-breakpoint-up(xl){
          
        }
        .logo img{
            width:85%;
        }
    }
}

.rotateLogo{animation: rotation 2s linear infinite; backface-visibility: hidden; transform-origin: center center;}
@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.hamburger {
    position: relative;
    height: 61px;
    cursor: pointer;
    fill: none;
    stroke-width: 4px;
    stroke: currentColor;
    margin-right: -15px;
    margin-top: -20px;
    margin-bottom: -20px;
}

.hm {
    display: flex;
    flex-direction: column;
    width: 36px;
    cursor: pointer;
    overflow: hidden;
    color: $secondary !important;
    span {
        background: $secondary;
        border-radius: 10px;
        height: 3px;
        margin: 3px 0;
        transition: .4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
        &:nth-of-type(1) {
            width: 50%;
        }
        &:nth-of-type(2) {
            width: 100%;
        }
        &:nth-of-type(3) {
            width: 75%;
        }
    }   
}



.min-height-0{
    min-height: 0 !important;
}


.main-nav {
    >ul {      
        >li {
            position: relative;
            @include font-size(16px);               
            > a {
                display: block;               
                color: $black;
                font-weight: 400;
                padding: 0 15px;
                text-align: center;
                text-decoration: none !important;
                transition: all 0.3s;
                @include media-breakpoint-up(xxl){
                    padding: 0 18px;
                }
            }           
            &:hover,
            &.active {
                > a { 
                    color: $secondary
                }
            }
            
        }
    }
}
.topBtn{
    background:$primary;
    color: $white !important;   
    padding: 7px 18px !important;
    border-radius: 50px;
    display: inline-block !important;
    min-width: 150px;
}

.submenu {        
    @include media-breakpoint-up(xl){
        position: absolute;
        left: 0;
        top: 0;
        padding-top: 62px; 
        visibility: hidden;
        opacity: 0; 
        white-space: nowrap;
        transition: all 0.3s;        
    } 
    @include media-breakpoint-down(xl){
        padding-left: 15px;
        border-left: 1px solid $primaryLight;
    }     
    ul {
        background-color: $primary;
        border-radius: 10px;
        overflow: hidden;
        min-width: 100%; 
        list-style: none;
        margin: 0;
        padding: 15px 0;
        li {
            position: relative; 
            a {               
                display: block;
                padding: 10px 0;
                color: $white;
                text-decoration: none !important;                  
                transition: all 0.3s;
                text-transform: none;
                font-size: 13px;
                
                @include media-breakpoint-up(xl){
                    padding: 10px 20px;  
                } 
                @include media-breakpoint-down(xl){
                    &.active,
                    &:hover{          
                        color: $white;
                    }
                }
            }
            .submenu {
                left: 100%;
                top: 0;
                padding-top: 0;               
            }

            &:hover{                
               > a{
                     @include media-breakpoint-up(xl){
                        background-color: $primaryLight;
                     }
                }                              
            }
        }
    }
    &:before{
        content:'';
        width: 20px;
        position: absolute;
        height: 20px;
        top: 40px;
        left: 20px;
        border:12px solid $primary;
        border-left-color: transparent;
        border-right-color: transparent;
        border-top-color: transparent;
    }
}
.header-main.active{
    .submenu{
        padding-top: 52px !important;
    }
    .submenu:before{
        top: 30px !important;
    }
}
.main-nav{
    li{
        &:hover{
           > .submenu {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}




.btn-nav{   
    &:hover{
      
    }
}


.logo { 
    img,svg {
        width: 100%;
        height: auto;
        max-width: 180px;
        display: block;
        transition: 0.3s ease all;
    }
}



.chbox {
    overflow: hidden;
    input[type="checkbox"] {
       opacity: 0;
       position: absolute;
       z-index: -99;
    }
    label {
        display: inline-flex;
        align-items: center;
        font-weight: normal;
        position: relative;      
        line-height: 1.2;
        color: $white;
        cursor: pointer;
        margin-bottom: 0;
        user-select: none;
        font-weight: normal;        
        &:before,
        &:after {
            content: "";          
            text-align: center;          
            width: 16px;
            max-width: 16px;
            height: 16px;          
            border: 1px solid $white;    
            margin-right: 16px;  
            border-radius: 50%;     
            background-color: $primaryLight;
        }

        &:after {
            position: absolute;
            border: 0;
            background-color: $white;
            transform: scale(0);
            transition: transform 0.2s;
            box-shadow: 0 0 2px #000;
        }
        
    }
    input[type="checkbox"]{
     &:focus-visible{
        + label{           
            outline: $accent1 auto 1px;
        }
     }
     &:checked{
        + label {
            &:after {                
                transform: scale(0.4);
            }          
        }
     }
    }
}

.spinner {
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    position: relative;
}

.spinner:after,
.spinner:before {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $primary;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    animation: sk-bounce 2s infinite ease-in-out;
}

.spinner:after {
    animation-delay: -1s;
}

@keyframes sk-bounce {
    0%,
    100% {
        transform: scale(0);
    }
    50% {
        transform: scale(1);
    }
}

.fancybox__container{
   audio,video{
    outline: none !important;
   }   
}


.fancybox__container{
    --fancybox-bg: rgba(8, 23, 40, 0.8);
}
.fancybox__content>.f-button.is-close-btn,
.is-compact .fancybox__content>.f-button.is-close-btn{
    --f-button-svg-width: 32px;
    --f-button-svg-height: 32px;
    top: 25px !important;
    right: 25px !important;
    color: $secondary;
    opacity: 1;
    background-color: transparent !important;
}

#fancybox-team{
    .fancybox__content>.f-button.is-close-btn,
    .is-compact .fancybox__content>.f-button.is-close-btn{
        color: $primary !important;
    }
}